import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { LoginComponent } from './pages/login/login.component';
import { SignupComponent } from './pages/signup/signup.component';
import { Dash2Component } from './pages/dash2/dash2.component';
import { Complete1Component } from './pages/complete1/complete1.component';

const routes: Routes = [
  { path: '', redirectTo: 'dash2', pathMatch: 'full' },
  { path: 'home', component: DashboardComponent, data: { state: 'home'} },
  { path: 'login', component: LoginComponent, data: { state: 'login'} },
  { path: 'signup', component: SignupComponent, data: { state: 'signup'} },
  { path: 'dash2', component: Dash2Component, data: { state: 'dash2'} },
  { path: 'complete1', component: Complete1Component, data: { state: 'complete1'} },
  { path: '**', component: DashboardComponent }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
